*{
  font-family: Arial, Helvetica, sans-serif;
}

body{
  padding:60px;
}
input{
  font-size: 1.2rem;
  width: 100%;
  color:grey;
}

.card-grid{
 display: flex;
 flex-direction: row;
 flex-wrap: wrap; 
}

.card {
  align-content: start;
  border:1px solid grey;
  border-radius: 3px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right:10px;
  width: 50%;
}

.card p {
  padding: 2px;
  text-align: start;
}

.card img {
 max-height: 250px;
 width:70% ;
}

